* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  font-family: "DM Sans", sans-serif;
}

.container {
  background: url("../../images/home-bg-container.jpg") no-repeat center/cover;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-items: center;
}

.main {
  width: 500px;
  background: rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  position: absolute;
  top: 70%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  padding: 0 30px 50px;
  color: #2c2f24;
}

.main img {
  width: 100px;
  margin-top: -50px;
  border-radius: 50%;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  background-color: #ad343e;
}

.main h2 {
  font-size: 38px;
  font-weight: 500;
  margin: 30px 0 10px;
}

.main p {
  margin-top: 20px;
}

.main button {
  width: 90%;
  margin-top: 30px;
  padding: 15px 0;
  background-color: #ad343e;
  color: #fff;
  border: 1px solid #ad343e;
  outline: none;
  font-size: 18px;
  border-radius: 20px;
  cursor: pointer;
  box-shadow: 0 5px 5px rgba(0, 0, 0, 0.2);
}

.main button:hover {
  background-color: #982831;
}

@media (max-width: 600px) {
  .main {
    width: 90%;
    padding: 0 20px 40px;
  }

  .main img {
    width: 80px;
    margin-top: -40px;
  }

  .main h2 {
    font-size: 28px;
    margin: 25px 0 8px;
  }

  .main button {
    width: 100%;
    padding: 12px 0;
    font-size: 16px;
  }
}

@media (max-width: 400px) {
  .main h2 {
    font-size: 24px;
  }

  .main p {
    font-size: 15px;
  }

  .main button {
    padding: 10px 0;
  }
}
