
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.text {
  font-size: 18px;
  line-height: 28px;
  color: #414536;
}

.content {
  font-family: "DM Sans", sans-serif;
  background-color: #f9f9f7;
  padding: 5% 15%;
  margin: 0 auto;
}

h2 {
  font-family: "Playfair Display", serif;
  font-weight: 500;
  font-size: 55px;
  line-height: 60.5px;
  text-align: center;
  color: #2c2f24;
  margin-bottom: 40px;
}

.pageImg {
  width: 100%;
  height: auto;
  display: block;
}

.imgContainer {
  width: 100%;
  max-width: 1200px;
  margin-bottom: 40px;
}

.textContainer {
  margin-bottom: 40px;
}

.textContainer > div {
  margin-bottom: 40px;
}

.textContainer h3 {
  font-weight: 700;
  font-size: 24px;
  line-height: 30px;
  letter-spacing: -3%;
  color: #2c2f24;
  margin-bottom: 20px;
}

.textContainer p {
  margin-bottom: 20px;
}

.textContainer span {
  font-weight: 700;
}

.articles {
  padding: 6% 10%;
  margin: auto;
  background-color: #fffffe;
}

.articlesInfo {
  margin-bottom: 40px;
}

.pArticles {
  text-align: center;
}

.cardContainer {
  display: flex;
  flex-wrap: wrap;
  gap: 1.2rem;
  justify-content: center;
}

.card {
  border-radius: 10px;
  width: 280px;
  background-color: #fff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
  transition: transform 0.2s ease-in-out;
}

.card:hover {
  transform: translateY(-5px);
}

.card img {
  width: 100%;
  height: 180px;
  object-fit: cover;
}

.cardContent {
  padding: 8%;
}

.cardContent .date {
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  margin-bottom: 10px;
  color: #737865;
}

.cardContent .title {
  font-size: 18px;
  line-height: 26px;
  font-weight: 500;
  color: #2c2f24;
}

.backBtn {
  background-color: #ad343e;
  color: #fff;
  padding: 30px 40px;
  margin-top: 50px;
  margin-left: 10px;
  text-align: center;
  border: 1px solid #ad343e;
  border-radius: 50px;
}

.backBtn:hover {
  cursor: pointer;
  background-color: #8b2830;
}

@media (max-width: 1024px) {
  h2 {
    font-size: 44px;
    line-height: 51px;
    font-weight: 600;
  }
}

@media (max-width: 768px) {
  h2 {
    font-size: 34px;
    line-height: 40px;
    font-weight: 700;
  }
}

@media (max-width: 480px) {
  h2 {
    font-size: 28px;
    line-height: 34px;
    font-weight: 800;
  }
}
