.services-section {
  max-width: 1200px;
  margin: auto;
  text-align: center;
  margin-bottom: 100px;
  margin-top: 100px;
}

.services-section h2 {
  font-size: 36px;
  margin-bottom: 40px;
  font-weight: bold;
}

.services-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 40px;
}

.service-card {
  flex: 1 1 240px;
  max-width: 300px;
  min-width: 200px;
  text-align: center;
}

.service-card img {
  width: 100%;
  height: 220px;
  object-fit: cover;
  border-radius: 12px;
  margin-bottom: 15px;
}

.service-card h3 {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 8px;
}

.service-card p {
  font-size: 14px;
  color: #555;
  line-height: 1.5;
}
