* {
  box-sizing: border-box;
}

/* sec2-homePage */
.sec2-homePage {
  padding: 40px;
}

.sec2-h {
  width: 100%;
  text-align: center;
  font-size: 55px;
  font-weight: normal;
  font-family: "Playfair Display";
  margin-bottom: 40px;
  color: #2c2f24;
}

.cards-container {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
  max-width: 1280px;
  margin: auto;
  padding: 20px;
}

@media (min-width: 600px) {
  .cards-container {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 768px) {
  .cards-container {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (max-width: 500px) {
  .sec2-homePage {
    padding: 20px 10px;
  }
  .sec2-h {
    font-size: 28px;
    margin-bottom: 20px;
    padding: 0 10px;
  }
  .card {
    height: auto;
    padding: 15px;
    border-radius: 15px;
  }
  .card img {
    width: 80px;
    height: 80px;
  }
  .card h4 {
    font-size: 18px;
    margin: 10px 0;
  }
  .card p {
    font-size: 14px;
    padding: 0 5px;
  }
  .explore-btn {
    font-size: 13px;
    padding: 8px 16px;
    width: 100%;
  }
  .cards-container {
    padding: 10px;
  }
}

.card {
  height: 375px;
  margin: 0 auto;
  border: 1px solid #dbdfd0;
  border-radius: 20px;
  text-align: center;
  padding: 20px;
}

.card img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-top: 10px;
}

.card h4 {
  font-family: "DM Sans";
  font-weight: 700;
  font-size: 20px;
  line-height: 30px;
  margin: 15px;
  color: #2c2f24;
}

.card p {
  font-family: "DM Sans";
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #414536;
  margin-bottom: 20px;
  padding: 0 10px;
}

.explore-btn {
  font-weight: 700;
  font-size: 14px;
  padding: 10px 20px;
  color: #ad343e;
  background-color: transparent;
  border: 1px hidden #ad343e;
  border-radius: 5px;
  text-decoration: none;
  margin-top: 10px;
}

.explore-btn:hover {
  background-color: #ad343e;
  color: #fff;
}

/* sec3-homePage */

.sec3-homePage {
  display: flex;
  flex-direction: row;
  width: 100%;
  padding: 50px 20px;
  gap: 90px;
  background-color: #f9f9f7;
  position: relative;
}

.sec3-homePage img {
  width: 649px;
  height: auto;
  border-radius: 10px;
}

.contact-box {
  position: absolute;
  left: 420px;
  bottom: 20px;
  width: 300px;
  background-color: #474747;
  color: white;
  border-radius: 12px;
  padding: 20px;
  font-family: "DM Sans";
}

.contact-box h4 {
  font-size: 24px;
  padding-bottom: 15px;
}

.contact-box .info i {
  color: white;
  margin-right: 10px;
  display: inline-block;
  vertical-align: top;
  font-size: 16px;
  width: 20px;
  text-align: center;
}
.contact-box .info p {
  color: #f9f9f7;
  display: inline-block;
  font-family: "DM Sans";
  font-size: 16px;
  line-height: 24px;
}

.right-text {
  width: calc(100% - 649px - 90px);
  /* 90px gap */
}

.right-text h2 {
  font-family: "Playfair Display";
  font-weight: 500;
  font-size: 55px;
  line-height: 60.5px;
  font-weight: normal;
  color: #2c2f24;
  text-align: left;
}

.right-text .first-paragraph {
  font-family: "DM Sans";
  font-weight: 500;
  font-size: 18px;
  line-height: 28px;
  color: #2c2f24;
}

.right-text .sec-paragraph {
  font-family: "DM Sans";
  font-weight: 400;
  line-height: 24px;
  color: #414536;
}

.button-link {
  width: 179px;
  height: 64px;
  border-radius: 118px;
  padding: 20px 32px;
  border: 1.5px solid #2c2f24;
  background: none;
  color: #2c2f24;
  font-weight: 600;
}

.button-link:hover {
  background-color: #2c2f24;
  color: white;
}

/* tablet */
@media (max-width: 900px) {
  .sec3-homePage {
    flex-direction: column;
    gap: 25px;
    padding: 30px 20px;
  }
  .sec3-homePage img {
    width: 100%;
    height: auto;
    /* object-fit: cover;
        max-height: 400px; */
  }
  .contact-box {
    position: static;
    width: 100%;
    margin-top: 20px;
  }
  .right-text {
    width: 100%;
  }
  .right-text h2 {
    font-size: 36px;
    line-height: 48px;
  }
  .right-text .first-paragraph,
  .right-text .sec-paragraph {
    font-size: 16px;
    line-height: 24px;
  }
}

/* Mobile */
@media (max-width: 600px) {
  .sec3-homePage {
    flex-direction: column;
    gap: 20px;
    padding: 20px;
  }
  .sec3-homePage img {
    width: 100%;
    max-width: 100%;
    height: auto;
  }
  .contact-box {
    position: static;
    width: 100%;
    padding: 16px;
    margin-top: 10px;
  }
  .right-text {
    width: 100%;
  }
  .right-text h2 {
    font-size: 28px;
    line-height: 36px;
  }
  .right-text .first-paragraph,
  .right-text .sec-paragraph {
    font-size: 15px;
    line-height: 22px;
  }
  .button-link {
    width: 100%;
    padding: 16px;
  }
}
